:root{

  --dark-bg-color:#000000;
  --light-bg-color:#fff;
  --light-text-color:#fff;
  --dark-text-color:#000000;
  --accentRed:#ff1616;
  --accentGray:#d9d9d9;
  --accentRedHover:rgb(255, 0, 0);

}

