body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;    
}
.App{
    flex-direction:column;
    align-items: center;    
    
}

.hamburger{display: none;}
.navBarLinks{
    display: flex;
    align-items: center;
    flex-direction: row;
}
.navBarLinks a{
    color: var(--light-text-color);
}
.item::after{
    position: relative;
    left: 0;
    right: 0;
    width: 0%;
    content: '';
    color: transparent;
    background: #aaa;
    height: 2px;
    display: block;
    transition: width 1s;
    bottom: -5px;
}
.item:hover::after{
    width: 100%;
}
.navBarLinks ul li{
    list-style: none;
    margin: 10px;
    cursor: pointer;
    font-size: 1.1em;
}
nav{
    background-color: var(--dark-bg-color);
    display: flex;
    width: 100%;
    height: 15vh;
    border-radius: 0 0 20px 20px;
    top: 0;
    position: fixed;
    color: var(--light-text-color);
    justify-content: space-around;
    box-shadow:
    0px 1.8px 2.2px rgba(80,80,80, 0.02),
    0px 4.3px 5.3px rgba(80,80,80, 0.028),
    0px 8px 10px rgba(80,80,80, 0.035),
    0px 14.3px 17.9px rgba(80,80,80, 0.042),
    0px 26.7px 33.4px rgba(80,80,80, 0.05),
    0px 64px 80px rgba(80,80,80, 0.07);
}

.logoNavBar{
    height: inherit;
    margin-right: 30px;
}
.navBarLinks ul{
    display: flex;
    flex-direction: row;
}

#subshow{
    position: absolute;
    z-index: 1;
    background-color: var(--dark-bg-color);
    flex-direction: column;
    height: auto;
    justify-content: center;
    border: 2px solid black;
    border-radius: 0 0 10px 10px;
    overflow: hidden;
    
}
#subshow li{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin:0;
    transition: background-color 0.5s;
}
#subshow li a{
    display: block;
    padding: 15px;
}
#subshow li a::after{
    display: none;
}
#subshow li:hover{
    background-color: var(--accentRed);
}
#subhidden{
    display: none;
    max-height: 0;
}

@media screen and (max-width: 768px) {
    .navBarLinks ul li a::after{
        display: none;
    }
    #subshow{
        position: inherit;
        align-items: baseline;
        background-color: var(--dark-bg-color);
        flex-direction: column;
        justify-content: center;
        width: 100%;
        z-index: 1;
        transition:3s all ;
        grid-row-gap: 100%;
        row-gap: 100%;
        margin-left: 20px;
        margin-bottom: 0;
        grid-row-gap: 10px;
        row-gap: 10px;
    }
    #subshow li{
        width: 90%;

    }
 
    #subhidden{
        display: none;
    }
 
    .hamburger{
        display: block;
        margin-left: 30px;
        cursor: pointer;
        color: var(--light-text-color);
        -webkit-tap-highlight-color: transparent;
    }
    nav{
        z-index: 1; 
        align-items: center;
        justify-content: space-between;
        color: wheat;
        height: 12vh;
        width: 100%;
    }
   nav #show {
    overflow: hidden;

        flex-direction: column;
        grid-row-gap: 30px;
        row-gap: 30px;
        justify-content: center;
        align-items: center;
        width: 100%;
        top: 12vh;
        position: absolute;
        background-color: var(--dark-bg-color);
        height: auto;    
        border-bottom-left-radius: 20px;    
        border-bottom-right-radius: 20px;  
        color: white;
        z-index: 10;
   }
  
   .navBarLinks{
    -webkit-tap-highlight-color: transparent;
        overflow-y: scroll;
        overflow-x: hidden;
        box-shadow:
        0px 1.8px 2.2px rgba(80,80,80, 0.02),
        0px 4.3px 5.3px rgba(80,80,80, 0.028),
        0px 8px 10px rgba(80,80,80, 0.035),
        0px 14.3px 17.9px rgba(80,80,80, 0.042),
        0px 26.7px 33.4px rgba(80,80,80, 0.05),
        0px 64px 80px rgba(80,80,80, 0.07);
        font-size: 1.2em;
    }
    nav #hidden{
        display: none;
    }
    .navBarLinks ul{
        list-style: none;
        padding: 0;
        flex-direction: column;
        width: 100%;
        grid-row-gap: 7px;
        row-gap: 7px;
        padding: 5%;
    }
    
    .navBarLinks ul a{
        color: var(--light-text-color);
    }
    .navBarLinks li{
        
        font-size:3em;
        margin: 20px;
    }
    .navbarLinks li::after,.navbarLinks ul a::after{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 0%;
        content: '';
        color: white;
        background-color:white;
        height: 1px;
    }
    .item::after{
        display: none;
    }
    .navbarLinks ul a:hover::after, navbarLinks li:hover::after{
        width: 100%;
    }
  
    .blurryBack{
        opacity: 0.5;
        height: 100%;
        width: 100%;
        background-color: var(--dark-bg-color);
        position: fixed;
        z-index: 2;
    }
    .logoNavBar{
        position:absolute;
        top:0;
        left:50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}


footer{
    background-color: var(--light-bg-color);
    color: black;
    height: 22vh;
    border-radius: 20px 20px 0 0;
    display: flex;
    flex-direction: column;
    grid-row-gap: 30px;
    row-gap: 30px;
    width: 50%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;
}
footer h2{
    margin: 0;
    padding: 30px 0 0 30px;
    font-size: 1.5em;
}
footer h2::after{

    display: block; 
    content: '';
    width: 50px;
    height: 10px;
    background-color: var(--accentRed);
    margin-top: 10px;
    box-shadow: rgba(255, 0, 0, 0.5) 0px 7px 29px 0px;

}
.footerWrapper{  
    width: 100%;
    background-color: var(--light-bg-color);            
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 20px;
    font-size: 2em;

}
 .iconFooter{
    color: white;
}
.icon{
    color: black;
    transition:all 1s;
    cursor: pointer;
   
}
#locationIcon{
    position: relative; 
}
#locationIcon:hover::after{

    opacity: 1;
}   
#locationIcon::after{
    transition: opacity 1s;
    opacity: 0; 
    content: 'Lagomar, Canelones';
    position: absolute;
    color: white;
    background-color:red;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px;
    bottom: 120%;
    left: -30px;
    font-size: 0.4em;
}
.icon:hover{color: rgb(60, 60, 60);}

