.divServicio{
    grid-row-gap: 2.4vh;
    row-gap: 2.4vh;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-evenly;
    margin-top: 6vh;
    height: 80vh;
    background: linear-gradient(to top, rgba(248, 248, 248, 0.7) 0%, rgba(248, 248, 248, 1) 100%);
    border-radius: 15px;
    padding: 10%;
    max-height: 700px;
    
}
.btnMasInformacion{
    align-self: center;

    width: 70%;
    min-height: 8%;
    border: none;
    border-radius: 25px;
    background-color: var(--accentRed);
    font-Size:1.1em;
    box-shadow: rgba(255, 0, 0, 0.5) 0px 7px 29px 0px;

}
.titleServicio{
    color: black;
    align-items: center;
    font-size:2em;
}
a{
    text-decoration: none;
}

.imgServicio{
    align-self: center;
    object-fit: contain;
    height: 70%;
