}
@media screen and (min-width:900px) {
    
    .divServicio{
        display: grid;
        height: 50vh;
        grid-template-columns: 50% 50%;
        grid-template-rows: 50% 50%;
        align-items: center;
        max-height: -webkit-max-content;
        max-height: max-content;
        justify-items: center;

    }
    .titleServicio{
        grid-column: 1/2;
        grid-row: 1/2;
        font-size: 2em;
    }
    .titleServicio::after{
        display: block;
        position: relative;
        bottom: -10px;
        content: '';
        width: 50%;
        height: 4px;
        background-color: var(--accentRed);
    }
    .imgServicio{
        grid-column: 2/3;
        grid-row: 1/3;
        height: 100%;
    }
    .btnMasInformacion{
        width: 65%;
        height: 45%;
        max-height: 70px;
        border: none;
        border-radius: 25px;
        background-color: var(--accentRed);
        font-Size:1.1em;
        cursor: pointer;
        box-shadow: rgba(255, 0, 0, 0.5) 0px 7px 29px 0px;
    }
    .btnMasInformacion:hover{
        background-color: var(--accentRedHover);
    }
}
