.paragraph {
  overflow: auto;
  transition: max-height 0.3s ease-out;
  height: auto;
  max-height: 600px;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.infoHide.paragraph::after{
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50%;
  background-image:linear-gradient(to top, rgba(255, 255, 255, 1), rgba(0,0,0,0));
}
.paragraph p {line-height: 120%;}
.infoHide {max-height: 200px; overflow: hidden;}
.showMore {
  border-radius: 50px;
  background: #FFFFFF;
  box-shadow: 5px 5px 13px #d3d3d3,
              -5px -5px 13px #ededed;
  border: none;
  min-width: 20%;
  font-size: 1em;
  margin-top: 3%;
  padding: 4% 6%;
  align-self: center;
  transition: all 1s;
  cursor: pointer;
}
.showMore:focus {outline: none;}
